.form--button{
    grid-column: 1/-1;
    font-family: fantasy;
    padding: auto;
    background: linear-gradient(90.41deg, #081177 1.14%, #04007a 100%);
    color: white;
    border: none;
    cursor: pointer;
    height: 50px;
    border-radius: 5px;
}
.form--input{
    text-align: center;
    align-items: center;
    display: inline-block;
    font-family: fantasy;
    height: 30px;
    border-radius: 5px;
}

.meme {
    position: relative;
}

.meme--image {
    max-width: 100%;
    display: flex;
    margin: auto;
    text-align: center;
}

.my--form{
    padding: 20px;
    display: grid;
    grid-template: 1fr 1fr;
    gap: 20px;
    background-color: rgb(74, 68, 68);
}

.meme--text {
    position: absolute;
    width: 80%;
    text-align: center;
    left: 50%;
    transform: translateX(-50%);
    margin: 15px 0;
    padding: 0 5px;
    font-family: impact, sans-serif;
    font-size: 2em;
    text-transform: uppercase;
    color: white;
    letter-spacing: 1px;
    text-shadow:
        2px 2px 0 #000,
        -2px -2px 0 #000,
        2px -2px 0 #000,
        -2px 2px 0 #000,
        0 2px 0 #000,
        2px 0 0 #000,
        0 -2px 0 #000,
        -2px 0 0 #000,
        2px 2px 5px #000;
}

.bottom {
    bottom: 0;
}

.top {
    top: 0;
}
